/* App.css */

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.app-container {
  display: flex;
  justify-content: space-around;
  padding: 20px;
}

.info-box {
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  text-align: center;
}

.info-box h1 {
  font-size: 1.8em;
  color: #3498db;
  margin-bottom: 15px;
}

.node-box {
  background-color: #3498db;
  color: #fff;
  border-radius: 8px;
  margin-top: 15px;
  padding: 15px;
}

.node-box ul {
  list-style-type: none;
  padding: 0;
}

.node-box li {
  margin-bottom: 10px;
}

.waiting-message {
  font-style: italic;
  color: #777;
}